import interfaceurl from '@/utils/interface.js'
import authPage from '@/components/authorization-page.vue' //引入授权窗体
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	components: {
		authPage
	},
	computed: mapState(['loginInfo', 'userInfo']),
	data() {
		return {
			title: '加入战队',
			info: {
				agentId: 0, //代理人ID
				nick: '', //代理人昵称
				head: '' //代理人头像
			},
			params: {
				sessionKey: '', //授权返回
				openId: '', //授权返回
				agentId: '' , //推荐人ID
				mobile: '' , //被邀请人手机号
				userInfo: {} //被邀请人授权信息
			}
		}
	},
	onLoad(options) {
		//this.info = options;
		if(!this.info.nick) {
			this.info = {
				agentId: 2, //代理人ID
				nick: '许松璋', //代理人昵称
				head: '' //代理人头像
			}
		}
	},
	watch: {
		loginInfo(newValue) {
			if (newValue.openId) {
				this.params.sessionKey = newValue.sessionKey;
				this.params.openId = newValue.openId;
				this.params.userInfo = this.userInfo;
				this.params.agentId = this.info.agentId;
				this.setJoinTeam();
			}
		}
	},
	methods: {
		//加入战队
		setJoinTeam () {
			if(!this.params.mobile) {
				uni.showToast({
				    title: '手机号不能为空',
				    icon: 'none',
				    duration: 2000
				});
				return;
			}
			let that = this;
			interfaceurl.checkAuth(interfaceurl.joinTeam, that.params).then((res) => {
				uni.showToast({
				    title: '加入成功',
				    icon: 'none',
				    duration: 2000
				});
				that.$turnPage('../index/login', 'reLaunch');
			});
		},
	}
}
